@import '../../fonts.scss';

.header{
  background: #ccc;
height: 1630px;
border-radius: 10px;
padding: 10px;

}
.navbar{
    height: 50px;
    position: fixed;
    z-index: 100;
    width: 100%;
     box-shadow: 1px 1px 30px 5px #ccc;
    .container{
        padding: 0 30px;
        display: flex;
        align-items: center;
        display: flex;
        justify-content: space-between;
        align-items: center;

.logo{
    font-family: 'Open Sans', sans-serif;
font-size: 23px;
font-weight: 600;
text-decoration: none;
color: #3A68B8;
display: flex;
align-items: center;

img{
    margin-right: 20px;
}


}

    .menu{
        display: flex;
        list-style-type: none;

        .menu__list{
margin-left: 30px;

.menu__link{
    text-decoration: none;
    font-family: 'Open Sans', sans-serif;
font-weight: 600;



}

        }
    }
    .select__lang, .select__theme{
font-weight: bold;
font-size: 17px;
outline: none;
margin-right: 15px;
        border: none;
    }
}
}
.intro{
width: 604px;
padding: 150px 0;

.intro__wrapper{
    display: flex;


.paragraph{
    font-family: 'Open Sans', sans-serif;
font-size: 18px;
font-style: normal;
font-weight: 600;


}

.intro__title{
    font-family: 'Open Sans', sans-serif;
font-size: 55px;
font-weight: 800;
line-height: 71px;

}

.intro__text{
    font-family: 'Open Sans', sans-serif;
font-size: 18px;
padding-top: 40px;
font-weight: 600;
width: 558px;

}


.intro__btn{
margin-right: 20px;
border-radius: 5px;
padding: 15px 30px 15px 30px;
background: #3A68B8;
color: white;
margin-top: 50px;
border: none;
font-family: 'Open Sans', sans-serif;
font-size: 20px;
font-weight: 600;

}

}

.logo__wrapper{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 70px;
    img{
        margin-right: 40px;
    }
}
}